import { init, axiosFactory, axiosPostFactory} from './axios-factory'

// 生成验证码图片
function getVerifyCode () {
  return axiosFactory({
    method: 'get',
    url: 'genverifycode'
  })
}

// 发送手机验证码
function getSendMsg (params) {
  return axiosFactory({
    method: 'get',
    url: 'sendmsg',
    params
  })
}

// 发送手机验证码
function getSendSms (params) {
  return axiosFactory({
    method: 'get',
    url: 'sendsms',
    params
  })
}

// 验证手机验证码
function getValideMsg (params) {
  return axiosFactory({
    method: 'get',
    url: 'validemsg',
    params
  })
}

// 获取熊猫数据
function getPets (params) {
  return axiosFactory({
    method: 'get',
    url: 'getPets',
    params
  })
}

// 获取熊猫属性
function getPetAttribute (params) {
  return axiosFactory({
    method: 'get',
    url: 'getPetAttribute',
    params
  })
}

// 捕获
function getCapture (params) {
  return axiosFactory({
    method: 'get',
    url: 'capture',
    params
  })
}

// 训练
function getTrain (params) {
  return axiosFactory({
    method: 'get',
    url: 'trainpet',
    params
  })
}

// 协议信息
function getAgreement (params) {
  return axiosFactory({
    method: 'get',
    url: 'agreement',
    params
  })
}

// 获取公告滚动内容
function getNotice () {
  return axiosFactory({
    method: 'get',
    url: 'notice'
  })
}

// 获取余额
function getBalance (params) {
  return axiosFactory({
    method: 'get',
    url: 'balance',
    params
  })
}

// 获取token
function getToken (params) {
  return axiosFactory({
    method: 'get',
    url: 'token',
    params
  })
}

// 绑定钱包地址
function getWallet (params) {
  return axiosFactory({
    method: 'get',
    url: 'setwallet',
    params
  })
}

// 查询交易记录
function getTransaction (params) {
  return axiosFactory({
    method: 'get',
    url: 'transaction',
    params
  })
}

function getLuckresult (params) {
  return axiosFactory({
    method: 'get',
    url: 'luckresult',
    params
  })
}

// 获取邀请链接等信息
function getInvite () {
  return axiosFactory({
    method: 'get',
    url: 'invitationcode'
  })
}

// 充值
function getRecharge () {
  return axiosFactory({
    method: 'get',
    url: 'recharge'
  })
}

// 出售
function getSell (params) {
  return axiosFactory({
    method: 'get',
    url: 'offser',
    params
  })
}

// 抽奖
function luckdraw (params) {
  return axiosFactory({
    method: 'get',
    url: 'luckdraw',
    params
  })
}

// 注册
function setRegist (data) {
  return axiosPostFactory({
    url: 'regist',
    data
  })
}

// 登录
function setLogin (data) {
  return axiosPostFactory({
    url: 'login',
    data
  })
}

// 忘记密码
function setForgetpassword (data) {
  return axiosPostFactory({
    url: 'forgetpassword',
    data
  })
}

// 修改昵称
function setModifyname (data) {
  return axiosPostFactory({
    url: 'modifyname',
    data
  })
}

// 上传头像
function setAvatarupload (data) {
  return axiosPostFactory({
    url: 'avatarupload',
    data
  })
}

let api = {
  init,
  getVerifyCode,
  getSendMsg,
  getSendSms,
  getValideMsg,
  getPets,
  getPetAttribute,
  getCapture,
  getTrain,
  getAgreement,
  getNotice,
  getBalance,
  getToken,
  getTransaction,
  getLuckresult,
  getInvite,
  getSell,
  luckdraw,
  setRegist,
  setLogin,
  setForgetpassword,
  getRecharge,
  setModifyname,
  setAvatarupload,
  getWallet
}

export default Object.assign({}, api)



