import * as types from './mutation-types'

import api from '@/api/index'

const fn = function () {}

const actions = {
  setUser ({ commit }, user) {
    commit(types.USER, user)
  },
  setUserSignin ({ commit }, user) {
    commit(types.USER_SIGNIN, user)
  },
  setUserSignout ({ commit }) {
    commit(types.USER_SIGNOUT)
  },
  setLotteryNum ({ commit }, data) {
    commit(types.LOTTERY_NUM, data)
  },
  setBalance ({ commit }, balance) {
    commit(types.BALANCE, balance)
  },
  setToken ({ commit }, { cb = fn }) {
    return api.getToken()
    .then((data) => {
      api.init({
        token: data
      })
      commit(types.TOKEN, data)
      commit(types.COUNTDOWN, 0)
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  setCountdown ({ commit }, data) {
    commit(types.COUNTDOWN, data)
  },
  setLogin ({ commit }, { cb = fn, userName, passWord }) {
    return api.setLogin({userName, passWord})
    .then((data) => {
      api.init({
        token: data.info.token
      })
      commit(types.USER, data.info)
      commit(types.USER_SIGNIN, data.info)
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  setChangeUser ({ commit }, data) {
    commit(types.USER, data)
    commit(types.USER_SIGNIN, data)
  },
  setEnglish ({ commit }, english) {
    commit(types.ENGLISH, english)
  },
  setCurrentPath ({ commit }, currentPath) {
    commit(types.CURRENT_PATH, currentPath)
  },
  getPets ({ commit }, { cb = fn, params }) {
    return api.getPets(params)
    .then((data) => {
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  getPetAttribute ({ commit }, { cb = fn, params }) {
    return api.getPetAttribute(params)
      .then((data) => {
        cb(null, data)
      })
      .catch(e => {
        cb(e)
      })
  },
  setPet ({ commit }, pet) {
    commit(types.PET, pet)
  },
  getAgreement ({ commit }, { params, cb = fn }) {
    return api.getAgreement(params)
    .then((data) => {
      commit(types.AGREEMENT, data.text)
      cb(null, data)
    })
    .catch((e) => {
      cb(e)
    })
  },
  getNotice ({ commit }, { cb = fn }) {
    return api.getNotice()
      .then((data) => {
        commit(types.NOTICE, data.text)
        cb(null, data)
      })
      .catch((e) => {
        cb(e)
      })
  },
  getCapture ({ commit }, { cb = fn, params}) {
    return api.getCapture(params)
      .then((data) => {
        cb(null, data)
      })
      .catch((e) => {
        cb(e)
      })
  },
  getSendMsg ({ commit }, { cb = fn, params }) {
    return api.getSendMsg(params)
      .then((data) => {
        cb(null, data)
      })
      .catch(e => {
        cb(e)
      })
  }
}

export default actions



