import api from '@/api/index'
import * as types from '../mutation-types'

const state = {
  transaction: {},
  invite: {},
  luckdrawHash: ''
}

const fn = function () {}

const actions = {
  // 获取交易记录
  getTransaction ({commit, state}, {cb = fn, params}) {
    return api.getTransaction(params)
    .then((data) => {
      commit(types.TRANSACTION, data)
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  // 获取图片验证码
  getCodebase64 ({ commit, state }, {cb = fn}) {
    return api.getVerifyCode()
    .then((data) => {
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  // 获取邀请好友链接等信息
  getInvite ({ commit, state }, { cb = fn }) {
    return api.getInvite()
    .then((data) => {
      commit(types.INVITE, data)
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  // 修改密码
  setForgetpassword ({ commit, state }, { cb = fn, params }) {
    return api.setForgetpassword(params)
    .then((data) => {
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  },
  // 注册
  setRegist ({ commit, state }, { cb = fn, params }) {
    return api.setRegist(params)
      .then((data) => {
        cb(null, data)
      })
      .catch(e => {
        cb(e)
      })
  },
  // 获取抽奖的 hash
  getLuckdrawHash ({ commit, state }, { cb = fn, params }) {
    return api.luckdraw(params)
    .then((data) => {
      commit(types.LUCKDRAW_HASH, data.txhash)
      cb(null, data)
    })
    .catch(e => {
      cb(e)
    })
  }
}

export const TRANSACTION = 'TRANSACTION'

const mutations = {
  [types.TRANSACTION] (states, data) {
    states.transaction = data
  },
  [types.INVITE] (states, data) {
    states.invite = data
  },
  [types.LUCKDRAW_HASH] (states, data) {
    states.luckdrawHash = data
  }
}

export default {
  state,
  actions,
  mutations
}



