import Vue from 'vue'
import { sync } from 'vuex-router-sync'
import App from './App'
import router from './router'
import store from './store/index.js'

/* 使用 axios */
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

// 使用 mint-ui
import Mint from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(Mint)

// // 使用 vue-i18n
// import i18n from 'vue-i18n'
// Vue.use(i18n)

import i18n from './utils/mixins/i18n'
import camelize from './utils/camelize'
import { Locale } from 'vant'

const demoBaseMixin = {
  beforeCreate () {
    const { name, i18n } = this.$options
    if (name && i18n) {
      const formattedI18n = {}
      const camelizedName = camelize(name)
      Object.keys(i18n).forEach(key => {
        formattedI18n[key] = { [camelizedName]: i18n[key] }
      })
      Locale.add(formattedI18n)
    }
  }
}

Vue.mixin(i18n)
Vue.mixin(demoBaseMixin)

// 引用全局样式文件
import './assets/scss/tsxm.scss'

sync(store, router)

let loc = window.location
if (loc.href.indexOf('regist') !== -1) {
  let index = loc.href.indexOf('=')
  window.sessionStorage.setItem('code', loc.href.slice(index + 1))
}

// 在每次路由跳转时都对 meta 和 token 进行验证，看是否有权限
router.beforeEach(({meta, path, name, params}, from, next) => {
  // console.log('meta, path ----> ', meta, path, name)
  if (meta.auth) { // 如果 meta.auth 为 true
    if (store.state.user.token) { // 通过 vuex state 获取当前的 token 是否存在
      // 如果有 token 那么跳到下一路由
      next()
    }
    else { // token 不存在
      // meta.auth 为true，但 token 不存在
      return next({ path: '/login' })
    }
  }
  else { // 如果 meta.auth 为 false, 除了那些不需要 meta.auth 为true 的页面，其他一律跳到登录页面
    if (path === '/login' || path === '/agreement') {
      next()
    }
    else {
      return next({ path: '/login' })
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



